@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.apply {
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  gap: 56px;
  padding-top: 72px;
  padding-bottom: 72px;
  display: flex;
}

@media (width >= 640px) {
  .apply {
    background-image: url("Shapes.79d49926.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: auto 80%;
    gap: 64px;
    padding-top: 200px;
    padding-bottom: 200px;
  }
}

.apply__text {
  flex-direction: column;
  align-items: center;
  gap: 24px;
  display: flex;
}

.apply__text__title {
  text-align: center;
  color: #253757;
  font-family: Poppins, sans-serif;
  font-size: 44px;
  line-height: 52.8px;
}

.apply__text__description {
  text-align: center;
  color: #6c788b;
  max-width: 700px;
  font-family: Open Sans, sans-serif;
  font-size: 22px;
  line-height: 33px;
}

h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.button {
  color: #fff;
  box-sizing: border-box;
  cursor: pointer;
  background-color: #2060f6;
  border: none;
  border-radius: 8px;
  width: 100%;
  height: 56px;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  line-height: 22.5px;
  transition: background-color .3s;
}

.button:hover {
  background-color: #144ed4;
}

@media (width >= 640px) {
  .button {
    width: 264px;
  }
}

.button:focus {
  background-color: #144ed4;
  outline: none;
}

.carousel {
  border-radius: 30px;
  align-self: flex-start;
  width: 100%;
  height: 100%;
  max-height: 390px;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 0;
}

@media (width >= 1024px) {
  .carousel {
    border-radius: 30px 0;
    width: 50vw;
    height: calc(100vh - 124px);
    max-height: none;
  }
}

@media (width >= 1200px) {
  .carousel {
    width: 45vw;
  }
}

.carousel__slide {
  opacity: 0;
  list-style: none;
  transition: opacity .3s ease-in-out .3s;
  position: absolute;
  inset: 0;
}

.carousel__slide[data-active] {
  opacity: 1;
  z-index: 1;
  transition-delay: 0s;
}

.carousel__slide > img {
  object-fit: cover;
  object-position: center;
  border-radius: 30px;
  width: 100%;
  height: 100%;
}

@media (width >= 1024px) {
  .carousel__slide > img {
    border-radius: 30px 0;
  }
}

.carousel__text {
  z-index: 2;
  flex-direction: column;
  align-self: flex-start;
  align-items: center;
  gap: 16px;
  height: auto;
  display: flex;
  position: absolute;
  bottom: 20px;
  left: 24px;
}

@media (width >= 640px) {
  .carousel__text {
    bottom: 41px;
    left: 32px;
  }
}

@media (width >= 1024px) {
  .carousel__text {
    flex-direction: row;
    gap: 24px;
    bottom: 45px;
    left: 48px;
  }
}

.carousel__text__controls {
  align-self: flex-start;
  gap: 16px;
  display: flex;
}

@media (width >= 1024px) {
  .carousel__text__controls {
    align-self: center;
  }
}

.carousel__text__controls__button {
  border: none;
  outline: 0;
  padding: 0;
}

.carousel__text__description {
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  max-width: 264px;
  display: flex;
}

.carousel__text__description__title {
  color: #fff;
  letter-spacing: 3px;
  text-align: center;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 700;
  line-height: 19.5px;
}

.carousel__text__description__p {
  color: #fff;
  text-align: left;
  font-size: 14px;
  font-weight: 400;
  line-height: 19.6px;
}

.footer {
  background-color: #2c2c2c;
  border-radius: 30px 30px 0 0;
  justify-content: center;
  width: 100%;
  padding-top: 72px;
  padding-bottom: 80px;
  display: flex;
}

@media (width >= 640px) {
  .footer {
    padding-top: 180px;
  }
}

.footer__content {
  row-gap: 80px;
  width: 100%;
  display: grid;
}

@media (width >= 1024px) {
  .footer__content {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 97px;
  }
}

@media (width >= 1200px) {
  .footer__content {
    column-gap: 226px;
  }
}

.footer__content__title {
  color: #fff;
  text-align: left;
  margin-bottom: 8px;
  font-size: 32px;
  line-height: 48px;
}

.footer__content__form {
  flex-direction: column;
  gap: 40px;
  max-width: 460px;
  display: flex;
}

@media (width >= 640px) {
  .footer__content__form {
    gap: 48px;
    max-width: 365px;
  }
}

@media (width >= 1024px) {
  .footer__content__form {
    grid-column: 1 / 2;
    max-width: none;
  }
}

.footer__content__form__title {
  padding-bottom: 8px;
}

.footer__content__form__input {
  text-align: left;
  color: #fff;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #fff;
  padding-bottom: 16px;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  line-height: 22.5px;
  transition: border-bottom .3s;
  position: relative;
}

.footer__content__form__input:hover {
  border-bottom: 1px solid #2060f6;
}

.footer__content__form__input:after {
  content: "";
  background-color: #fff;
  width: 100%;
  height: 2px;
  display: block;
  position: absolute;
  bottom: -8px;
}

.footer__content__form__input::placeholder {
  text-align: left;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
  line-height: 22.5px;
}

.footer__content__form__input:focus {
  border-bottom: 1px solid #2060f6;
  outline: none;
}

.footer__content__form__input:autofill {
  -webkit-text-fill-color: #fff;
  box-shadow: inset 0 0 0 50px #2c2c2c;
}

.footer__content__form__button {
  margin-top: 16px;
}

@media (width >= 640px) {
  .footer__content__form__button {
    margin-top: 40px;
  }
}

.footer__content__contacts {
  flex-direction: column;
  gap: 40px;
  max-width: 460px;
  display: flex;
}

@media (width >= 640px) {
  .footer__content__contacts {
    gap: 48px;
  }
}

@media (width >= 1024px) {
  .footer__content__contacts {
    grid-column: 2 / 3;
  }
}

.footer__content__contacts__contact {
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  display: flex;
}

.footer__content__contacts__contact__title {
  letter-spacing: 3px;
  text-align: left;
  text-transform: uppercase;
  color: gray;
  font-size: 13px;
  font-weight: 700;
  line-height: 19.5px;
}

.footer__content__contacts__contact__link {
  text-align: left;
  color: #fff;
  font-family: Open Sans, sans-serif;
  font-size: 24px;
  font-weight: 630;
  line-height: 36px;
  text-decoration: none;
  transition: all .3s;
}

.footer__content__contacts__contact__link:hover {
  color: #2060f6;
  transform: scale(1.1);
}

.footer__content__contacts__contact__socials {
  gap: 32px;
  display: flex;
}

.footer__content__bottom_bar {
  flex-direction: column;
  gap: 48px;
  display: flex;
}

@media (width >= 640px) {
  .footer__content__bottom_bar {
    gap: 24px;
  }
}

@media (width >= 1024px) {
  .footer__content__bottom_bar {
    flex-direction: row;
    justify-content: space-between;
  }
}

.footer__content__bottom_bar__links {
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  display: flex;
}

@media (width >= 640px) {
  .footer__content__bottom_bar__links {
    flex-direction: row;
    gap: 40px;
  }
}

@media (width >= 1024px) {
  .footer__content__bottom_bar__links {
    gap: 64px;
  }
}

.footer__content__bottom_bar__links__link {
  text-transform: uppercase;
  letter-spacing: 3px;
  text-align: left;
  color: #fff;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 19.5px;
  text-decoration: none;
  transition: transform .3s;
}

.footer__content__bottom_bar__links__link:hover {
  transform: scale(1.2);
}

@media (width >= 1024px) {
  .footer__content__bottom_bar {
    grid-column: 1 / 3;
  }
}

