.icon {
  box-sizing: border-box;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-origin: content-box;
  width: 24px;
  height: 24px;
  transition: all .3s;
}

.icon:hover {
  cursor: pointer;
  transform: scale(1.3);
}

.icon:focus {
  transform: none;
}

.icon--menu {
  background-image: url("icon_menu.6a8a2914.png");
  padding: 6px 3px;
}

@media (width >= 1024px) {
  .icon--menu {
    display: none;
  }
}

.icon--close {
  background-image: url("icon_close.569f41d6.png");
  padding: 2px;
}

.icon--quote {
  background-image: url("icon_quote.7987aca6.png");
  padding: 3px;
}

.icon--arrow {
  background-image: url("icon_arrow.e12f87b1.png");
  width: 40px;
  height: 40px;
}

.icon--arrow-l {
  background-image: url("icon_arrow_left.2a65397b.png");
  width: 32px;
  height: 32px;
}

.icon--arrow-l:hover {
  transform: scale(1.2);
}

.icon--arrow-l:focus {
  background-image: url("icon_arrow_left_dark.dc6cf056.png");
  transform: none;
}

.icon--arrow-r {
  background-image: url("icon_arrow_right.d3b85fbc.png");
  width: 32px;
  height: 32px;
}

.icon--arrow-r:hover {
  transform: scale(1.2);
}

.icon--arrow-r:focus {
  background-image: url("icon_arrow_right_dark.c6ea8720.png");
  transform: none;
}

.icon--logo {
  background-image: url("icon_logo.ec388db5.png");
  width: 46px;
  height: 28px;
}

.icon--social {
  width: 32px;
  height: 32px;
}

.icon--social--fb {
  background-image: url("icon_fb.bcb0ae20.png");
  padding: 4px 8.5px;
}

.icon--social--fb:hover {
  background-image: url("icon_fb_blue.66bd564d.png");
}

.icon--social--twt {
  background-image: url("icon_twt.2a63fce5.png");
  padding: 5.3px 4px;
}

.icon--social--twt:hover {
  background-image: url("icon_twt_blue.e7e4e824.png");
}

.icon--social--ig {
  background-image: url("icon_ig.455337cd.png");
  padding: 4px;
}

.icon--social--ig:hover {
  background-image: url("icon_ig_blue.4dd62203.png");
}

.icon--no-hover:hover {
  cursor: default;
  transform: none;
}

.menu {
  pointer-events: none;
  opacity: 0;
  z-index: 3;
  background-color: #2c2c2c;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  transition: all 1s;
  display: flex;
  position: fixed;
  top: 0;
  overflow: auto;
  transform: translateX(100%);
}

.menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.menu__links {
